
.backdrop{
    opacity: 1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    overflow-y: scroll;
    background-color: var(--backdrop-color);
  transition: opacity var(--transition-function) var(--timing-function);
}
.backdrop.is-hidden{
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    z-index: 5;
}
.modal-window{
    transform: translate(-50%, -50%) scale(1);
    background: var(--color-white);
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 40px;
    max-width: 100%;
    max-height: 80vh;

    background-image: url('../images/team-foto/dementor.png');
    background-repeat: no-repeat;
    background-position: left;
    background-size: contain;
    
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    @media screen and (min-width: 768px){
        max-width: 80%;
        width: 80vh;
    }
    
    @media screen and (min-width: 1024px) {
        width: 115vh;
        max-width: 100%;
    }
}
.modal-close__btn{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 6;


    width: 30px;
    height: 30px;
    background: var(--color-white);
    border: none;
    box-sizing: border-box;
}

.team_container {
    padding-top: 15px;
    height: 43vh;
    width: 40vh;
    @media screen and (min-width: 768px){
        display: flex;
        align-items: center;
        justify-content: space-between;
        align-content: center;
        height: 42vh;
        width: 68vh;
    }
    
    @media screen and (min-width: 1024px){
    max-height:40vh;
    width: 105vh;
}}
.team__title{
    font-size: 30px;
    text-align: center;
    color: var(--color-title);
    margin-bottom: 15px;
}
.team_card__img {
    // transform:perspective(800px) rotateY(25deg);
    padding: 20px 0;
    width: 150px;
    height: 200px;
    border-radius: 50%;
    transform-origin:center;
    transition:0.5s;
    .card &:hover {
        transform: scale(1.2);
        opacity:1;
    }
    @media screen and (min-width: 1024px) {
        object-fit:cover;
}
}
.name{
    color: var(--color-accent);
    padding-bottom: 10px;
}
.title{
    color: var(--color-accent);
}
.card{
    margin-left: 15px;

}
.github{
    display: inline-block;
    margin-right: 10px;
    background-color: var(--color-accent);
    border-radius: 50%;
    &:hover{
    transform: scale(1.5);
    }
}
.slick-next:before, .slick-prev:before{
    color: var(--color-dark);
}